body {
  background-color: #090A0E;
  color: white;
}

product-info {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */

}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;

  display: grid;
  gap: 15px;
  grid-template-columns: 1fr 1fr 1fr;
}

.product-cover {
  width: 300px;
  height: 300px;
}
